export default {
  name: 'World',
  props: {
        name: {
            type: String,
            required: true
        },
        sav: {
            type: Object,
            required: true
        },
        filters: {
            type: Object,
            required: true
        }
    },
  data () {
      return {
      }
  },
  computed: {
      card_class: function() {
          return 'card-' + this.name.toLowerCase() + ' world-card';
      },
      div_class: function() {
          return 'world-' + this.name.toLowerCase() + ' world-div';
      }
  }
}
